/* FONTS */

@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100&family=Sofia+Sans+Condensed:wght@100&family=Sriracha&display=swap");

/* GLOBAL STYLES */

:root {
  --font-primary: "Arial", Arial;
  --font-code: "Roboto", monospace;
  --bg-color: #161515;
  --font-color: #e7e7d8;
  --btn-bg-color: #443e3e;
  --accent-color: #382238;
  --header-shadow: #000 0px 0px 5px;
}

.App {
  text-align: center;
}

.App-header {
  background-color: var(--bg-color);
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: var(--font-primary);
  color: #000 0px 0px 5px;
}

body {
  background-color: var(--bg-color);
  display: flex;
  flex-flow: column;
  height: 100vh;
  color: var(--font-color);
  font-family: var(--font-primary);
}

/* Buttons */

.btn {
  display: inline-block;
  padding: 1rem 2rem;
  background: var(--btn-bg-color);
  color: var(--accent-color);
  font-family: var(--font-primary);
  font-style: italic;
  font-weight: bold;
  font-size: 1rem;
  text-transform: capitalize;
  transition: all 0.3s ease;
  box-shadow: 4px 4px 0 0 var(--accent-color);
  cursor: pointer;
  position: relative;
  text-decoration: none;
  outline: none;
}

.build-btn {
  /* styles for the send button */
  border-radius: 30px;
  font-size: 1rem;
  padding: 1rem 2rem;
  box-shadow: 4px 4px 0 0 #555;
  position: absolute;
  right: 10%;
  color: black;
  border-color: #555;
  transition: background-color 0.3s ease-in-out;
  cursor: pointer;
}

.build-btn:hover {
  box-shadow: 5px 5px 5px rgb(110, 110, 110);
  background-color: #6d6d6d;
}

.wallet-btn {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 4rem;
  margin-left: 2rem;
  border-color: transparent;
}

/* Attributes Design */

/* Style The Dropdown Button */

/* The container <div> - needed to position the dropdown content */
.text-input {
  padding: 8px;
  font-size: 15px;
  border-radius: 4px;
  border: 1px solid rgb(0, 0, 0);
  width: 100%;
  color: black;
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.formBox {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.2rem;
  padding: 8px;
  width: 500px;
  box-shadow: 0px 4px 6px 0px rgba(56, 56, 56, 0.2);
  background-color: #1f1f1f; /* set the background color */
}
.formBox > * {
  margin: 8px; /* add some margins to each element */
}

.label {
  font-size: 20px;
  font-weight: 400;
  grid-column: 1 / 2; /* takes the first column */
  margin-bottom: 2px;
  font-family: var(--font-primary);
}
.select {
  padding: 8px;
  font-size: 15px;
  border-radius: 4px;
  width: 100%;
  grid-column: 2 / 3; /* takes the second column */
  margin-top: 5px;
  color: black;


}

/* shows the message that pops up if the wallet is connected
or not.
*/
.centerDiv {
  position: absolute;
  top: 10px;
  left: 10px;
  display: flex;
  flex-direction: column;
}

.popup {
  position: absolute;
  top: calc(100% + 10px); /* adjust as needed */
  left : 25%;
  z-index: 1; /* Higher than other elements */
}

.popup-content p {
  white-space: pre-wrap;
}

.popup-content {
  padding: 12px;
  background-color: #212221;
  font-size: 1.1rem;
}


/* image design */

.image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50%; /* adjust height as needed */
  margin-top: 200px; /* add space between the popup and the image */
}

img {
  max-width: 25%;
  max-height: 25%;
}



